import React from "react";
import { useSelector, useDispatch } from "react-redux"
import { shopDeleteItem, allDeleteItem } from "../store";

const Cart = () => {
    let state = useSelector((state) => {return state});
    let dispatch = useDispatch();
    
    let arr = state.shopItem.map((value) => value.price);
    let avg = 0;
    for(let i = 0; i < arr.length; i++){
        avg += arr[i];
    }
    return(
        <div className="wrap">
            <h1 style={{textAlign: 'center', fontSize: '1.5rem'}}>{state.user} !</h1>
            <div className="cart-grid">
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                {state.shopItem.map((value, idx) => (
                    <React.Fragment key={idx}>
                        <div>{value.name}</div>
                        <div>{value.count}</div>
                        {/* store    --> store.js   (   ) */}
                        <div>{value.price}</div>
                        <div><button onClick={() => {
                            dispatch(shopDeleteItem(value.id))
                        }}></button></div>
                    </React.Fragment>
                ))}
                {state.shopItem.length ?
                <>
                    <div className="cart-avg"></div>
                    <div>{avg}</div>
                    <div onClick={() => {
                        dispatch(allDeleteItem());
                    }} style={{borderBottom: '1px solid lightgray'}}></div>
                </>
                : null}
            </div>
        </div>
    )
}
export default Cart;