import React, { useEffect, useState } from "react";
import { Outlet, useParams } from "react-router-dom";

// class    
class DetailTest extends React.Component{
    //   
    componentDidMount(){
    //   
    }componentDidUpdate(){
    //   
    }componentWillUnmount(){

    }
}
//       useEffect
//  useEffect   
//  ,    ,  
const PostDetail = ({img, item}) => {
    const [autoHide, setAutoHide] = useState(true);
    useEffect(() => {
        // setTimeout      
        let timeOut = setTimeout(() => {
            setAutoHide(false);
            console.log('false ');
        }, 3000);
        return() => {
            // clean up function  mount  x, unmount   
            //      
            clearTimeout(timeOut);
        }
    },[autoHide]);
    //  url    
    let {id} = useParams();
    // console.log(id);
    // find() -->        undefined 
    let value = item.find((item) => item.id == id);
    console.log(value);

    return(
        <div className="img-inner">
            {/* if  ,  id (  ) 404  */}
            <img src={process.env.PUBLIC_URL + `/img/${img[id]}.jpg`} alt=""/>
            <div style={{textAlign: 'center', height: '60px'}}>
                <p>{item[id].title}</p>
                <p>{item[id].content}</p>
                <p>{item[id].price}</p>
            </div>
            <button> </button>
            {autoHide ? <div style={{background: 'Yellow', width: '200px', height: '30px', margin: '0 auto'}}>3  </div> : null }
        </div>
    )
}

export default PostDetail;

