import { useState } from "react";
import Btn from '../components/Btn';



function AboutMe(){
    //  count 
    const [count, setCount] = useState([0, 0, 0]);
    const [write, setWrite] = useState([
        {
            title: '  ',
            content: '  4 ',
        },
        {
            title: '   ',
            content: '  ',
        },
        {
            title: '  930  ',
            content: '  ',
        }
    ]);
    const [objectTitle, setObjectTitle] = useState('');
    const [objectContent, setObjectContent] = useState('');
    const ObjectArrPush = () => {
        let copy = [...write];
        //    /..   .. ..
        let test = copy.length + 1;
        copy[test] = {
            title: objectTitle,
            content: objectContent
        }
        setWrite(copy);
    }

    //     
    const [arr, setArr] = useState([
        '3. ', 
        '1.  ', 
        '2.  '
    ]);
    //    
    const changeWrite = (e) => {
        e.stopPropagation();
        //   
        const test = e.target.key;
        console.log(test);
        //   key     ?
        // -->  if     
        let newArr = [...arr];
        newArr = [
            '3.   /  /   ',
            '1.  9 ',
            '2.          '
        ];
        setArr(newArr);
    }
    //  
    const kanadaChange = () => {
        const kanada = [...arr];
        kanada.sort();
        setArr(kanada);
    }

    // Modal  ,    
    const [show, setShow] = useState(false);
    // memo
    const [memo, setMemo] = useState('/memo props');
    //     
    const [arrShow, setArrShow] = useState(false);
    const [title, setTitle] = useState(0);
    // input     
    const [post, setPost] = useState('');
    const onChange = (e) =>{
        setPost(e.target.value);
    }
    const onPushArr = (e) => {
        let pushArr = [...arr];
        pushArr.push(post);
        // unshift     
        setArr(pushArr);
    }
    //    
    //  index      span    .. ..?
    const onSpliceArr = (e) => {
        e.stopPropagation();
        let copy = [...arr];
        console.log(copy);
        copy.splice(0, 1);
        console.log(copy);
        setArr(copy);
    }
    const [tab, setTab] = useState(0);
    return(
        <div className="wrap">
            <h1 className="blog">BLOG</h1>
            <ul className="flex">
                <li onClick={() => {
                    console.log(tab);
                    setTab(0)}}>Blog</li>
                <li onClick={() => {
                    console.log(tab);
                    setTab(1)}}>project</li>
                    {/*   +1  0   -->   */}
            </ul>
            {/*     if       */}
            <div className="p_div">
                {/* <Btn text={' '} onClick={kanadaChange}/> */}
                <button onClick={kanadaChange} className='btn_b'> </button>
                <br/>
                <div style={{display:'flex'}}>
                    <input type="text" style={{width: '100%'}} onChange={onChange} placeholder=" .  "/>
                    <button onClick={onPushArr} style={{ width: '200px'}}> </button>
                </div>
                {arr.map((arr, index) => (
                    <>
                        <p key={index} style={{display:'flex', justifyContent: 'space-between'}} onClick={() => {
                            setArrShow(true);
                            setTitle(index);
                            console.log(index);
                        }}>
                            {arr}
                            <div>
                                <span onClick={changeWrite}> </span>
                                <span style={{background: 'lightpink'}} onClick={onSpliceArr}> </span>
                            </div>
                        </p>
                        {/*     */}
                    </>
                    )
                )}
            </div>
            {
                arrShow ? (
                <div className="md_wrap" style={{background: 'lightgreen'}}>
                    {arr[title]}
                </div>
                ) : null
            }
            <div className="blog_inner">
                <div style={{width: '100%', display:'flex', alignItems:'center', marginBottom: '20px'}}>
                    <input type="text" onChange={(e) => {
                        setObjectTitle(e.target.value);
                    }} style={{width: '100%'}} placeholder="   "/>
                    <input type="text" onChange={(e) => {
                        setObjectContent(e.target.value);
                    }} style={{width: '100%'}} placeholder="   "/>
                    <button onClick={ObjectArrPush} style={{width: '120px', height: '40px', marginLeft: '20px'}}></button>
                </div>
                {write.map((write, index) => 
                <>
                    <p key={index} className="blog_title">
                        {write.title}
                        <span onClick={() => {
                            //  
                            const copy = [...count];
                            //   key(index)   1
                            copy[index] = copy[index] + 1;
                            //   copy state  
                            //    3 ? 
                            //     count    
                            //        
                            setCount(copy);
                        }}><span>({count[index]})</span></span>
                    </p>

                    {/*   key     .. */}
                    <div className="blog_content">
                        {write.content} <button> </button>
                    </div>
                </>
                )}
            </div>
            <button onClick={() => setShow(!show)}>Modal  ,  </button>
            {show ? 
            <>
                {/*    */}
                {/*     / ! */}
                <Modal arr={arr} bgColor={'skyblue'} onClick={kanadaChange}/>
            </> : null }
            <button className="mm_btn" onClick={() => setMemo(!memo)}>/</button>
            {/* show hide    */}
            {/* <Btn text={'/'} setBtnShow={setBtnShow}/> */}
            {memo ? null : <Memo memo={memo}/>}
            {/*   ..? */}
            {tab === 1 && <div>tab 2</div>}
        </div>
    )
}
