// store state,    import
import { useDispatch, useSelector } from "react-redux";
import { changeName, plus, plus2, changeArr, changeItemName, addItem, ascChange, deleteItem } from "../store";
//     import 

const Contact = () => {
    // store  state  
    // store      
    let state = useSelector((state) => {return state});
                        // store   state 
    // let state = useSelector((state) => return state.user);
                                //  state 
    // console.log(state);
    //       --> store.js    
    let dispatch = useDispatch();

    let obj = { name: 'dust'};
    JSON.stringify(obj); //   json   
    //   
    localStorage.setItem('data',JSON.stringify(obj));
    let test = localStorage.getItem('data');
    console.log(test.name); //     ->  JSON   
    let test2 = JSON.parse(test); //     
    console.log(test2.name);

    return(
        <div className="wrap">
            <h1 style={{textAlign: 'center', fontSize: '1.5rem'}}>{state.user} ! 
                <span onClick={() => { dispatch(changeName())}}></span>
            </h1>
            <div>
                {state.arr.age}
                <span onClick={() => {
                dispatch(changeArr())
            }}> </span>
            </div>
            <button onClick={() => {
                // ..
                dispatch(ascChange(state.cartItem))
            }}> </button>
            <div className="cart-grid">
                {/* <div>#</div> */}
                <div></div>
                <div></div>
                <div> </div>
                <div></div>
                {state.cartItem.map((value) => (
                    <>
                        {/* <div key={value.id}>{value.id}</div> */}
                        <div>{value.name}</div>
                        <div>{value.count}</div>
                        {/* store    --> store.js   (   ) */}
                        <div><button onClick={() => {
                            dispatch(plus(value.id));
                        }}>+</button></div>
                        <div><button onClick={() => {
                            dispatch(deleteItem(value))
                        }}></button></div>
                    </>
                ))}
                <button onClick={() => {
                    dispatch(changeItemName())
                }} className="btn">0  !</button>
            </div>
            <ContactDetail/>
        </div>
    )
}

const ContactDetail = () => {
    let state = useSelector((state) => {return state});
    let dispatch = useDispatch();

    return(
        <div style={{display: 'grid', gridTemplateColumns: '1fr 1fr 1fr', gridGap: '20px'}}>
            {state.cartItemDetail.map((value, idx) => (
                <div style={{width: '100%', height: '200px', background: 'lightpink', textAlign: 'center'}} key={idx}>
                    <img style={{width: '100%', height: '100%'}} src={process.env.PUBLIC_URL + `/img/2e7bac51f4d7d7f33379dae9ce79b0ec1c1773c4.gif`} alt="   "/>
                    <p> : {value.name}</p>
                    <button onClick={() => {
                        dispatch(addItem(value));
                    }}> </button>
                </div>
            ))}
        </div>
    )
}
export default Contact;