import { Routes, Route, Link, Outlet, useNavigate } from "react-router-dom";
import { useEffect, useRef } from "react";
function Study(){
    let navigate = useNavigate();
    let board = localStorage.getItem("BoardContent");
    board = JSON.parse(board);
    const search = useRef();
    useEffect(() => {
        search.current.focus();
        if(board === null){
            localStorage.setItem("BoardContent", JSON.stringify([]));
        }else{
            return
        }
    },[]);
    const onChange = (e) => {
        let text = e.target.value;
        for(let i = 0; i < board.length; i++){
            // console.log(board[i].title);
            let title = board[i].title;
            let search = title.indexOf(text);
            if(search > 0){
                console.log('!', title);
                console.log("search: ",search, "!");
                //      
            }else{

            }
        }
    }
    return(
        <>
            <div className="wrap">
                <div className="study-title">
                    <h1></h1>
                    <input onChange={onChange} ref={search} placeholder=" ."></input>
                </div>
                <button className="btn" onClick={() => navigate('studywrite')}></button>
                <div className="study-box">
                    {/*           */}
                    {/*   */}
                    <ul>
                        <li onClick={() => navigate(`studydetail`)} className="box">
                            <div className="title text-sort">
                                 .  ! (List)
                            </div>
                            <div className="content">
                                <p>
                                    <span></span>
                                    <span>2022.10.31</span>
                                    <span>(0)</span>
                                    <span>(0)</span>
                                </p>
                            </div>
                        </li>
                        {board.map((value, idx) => 
                            <li key={idx} onClick={() => navigate(`studydetail/${idx}`)} className="box">
                                <div className="title text-sort">
                                    {value.title}
                                </div>
                                <div className="content">
                                    <p>
                                        <span></span>
                                        <span>2022.10.31</span>
                                        <span>(0)</span>
                                        <span>({value.heart})</span>
                                    </p>
                                </div>
                            </li>
                        )}
                    </ul>
                </div>
            </div>
        </>
    )
}

export default Study;