import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import ShopBasket from "../components/ShopBasket";
import { btnSwitch, shopAddItem } from '../store';

const Shop = () => {
    let state = useSelector((state) => {return state});
    let dispatch = useDispatch();
    let navigate = useNavigate();

    useEffect(() => {
        let arr = localStorage.getItem("itemId");
        arr = JSON.parse(arr);
        if(Array.isArray(arr)){
            return
        }else{
            localStorage.setItem("itemId", JSON.stringify([]))
        }
    },[])

    return(
        <>
            <div className="wrap">
            <div className="shop-info" style={{marginBottom: '10px'}}>
                ,    !<br/>
                    ,   <br/>
            </div>
            {/* <img className="post-main-img" src={process.env.PUBLIC_URL + `/img/2e7bac51f4d7d7f33379dae9ce79b0ec1c1773c4.gif`} alt="   "/> */}
                <div className="shop-grid">
                    {state.shopImg.map((value, idx) => 
                    <div key={idx}>
                        <div onClick={() => {
                            navigate(`shopdetail/${idx}`);
                        }} className="img-div">
                            <img className="shop-item-img" src={process.env.PUBLIC_URL + `/img/${value.src}.jpg`} alt="" />
                        </div>
                        <p  onClick={() => navigate(`shopdetail/${idx}`)}>{value.name}</p>
                        <p>{value.price}</p>
                        <button onClick={(() => {
                            dispatch(shopAddItem(value));
                            dispatch(btnSwitch(true));
                        })} className="cart-btn"></button>
                    </div>
                    )}
                </div>
            </div>
            {state.close && <ShopBasket/>}
        </>
    )
}
export default Shop;