import { useEffect, useState } from "react";

const TestBox = ({ createBoxStyle }) => {
    const [style, setStyle] = useState({});
    console.log(style);

    useEffect(() => {
        console.log(' ')
        setStyle(createBoxStyle());
        //    setStyle  --> style
    }, [createBoxStyle]);
    //    ,  props    
    return (
        <div style={style}></div>
    )
}

export default TestBox;