import { useState, useRef, useEffect } from "react";
import axios from 'axios';
import { useQuery } from 'react-query'

const TodoList = () => {
    
    // ajax  react-query   
    // axios.get(`https://codingapple1.github.io/usedata.json`).then((json) => {})
    //    ( sns !)
    let result = useQuery('data', () => 
        axios.get('https://codingapple1.github.io/userdata.json')
        .then((value) => value.data)
    );
    //   ,      
    //     
    // props         

    // console.log(result);
    // console.log(result.data);
    // console.log(result.isLoading);
    // console.log(result.error);

    
    const inputValue = useRef();
    // const [todoCheck, setTodoCheck] = useState([]);
    const [todoList, setTodoList] = useState([]);
    // localStorage 
    let todo = localStorage.getItem("todoList");
    // todo  
    todo = JSON.parse(todo);
    useEffect(() => {
        inputValue.current.focus();
        if(todo == null){
            //   localStorage 
            localStorage.setItem("todoList", JSON.stringify([]));
        }else{
            setTodoList(todo);
        }
    },[])
    // todoList   enter 
    const onKeyPress = (e) => {
        let todos = e.target.value;
        if(e.key === 'Enter'){
            if(todos === ""){
                alert(' !')
            }else{
                //    ,    
                todo.unshift({text: todos, id: Date.now(), check: false});
                // id: Date.now()
                //   array
                todo = new Set(todo);
                //  array 
                todo = Array.from(todo);
                localStorage.setItem("todoList", JSON.stringify(todo));
                setTodoList(todo);
            }
            inputValue.current.value = '';
        }
    }
    // 
    const todoDelete = (idx, value) => {
        todo.splice(idx, 1);
        localStorage.setItem("todoList", JSON.stringify(todo));
        setTodoList(todo);
    }
    return(
        <div className="wrap">
            <h1 style={{marginBottom:'20px'}}> !</h1>
            <input type="text" placeholder="ToDo List ~!" style={{padding: '10px', height:'50px', fontSize: '18px'}}
            onKeyPress={onKeyPress} ref={inputValue}
             />
             <div className="todo-bg">
                <ul>
                    <li>
                        <input id="todo-test" type="checkbox"></input>
                        <label htmlFor="todo-test">
                            <p> !    !</p>
                            <p>
                                <span onClick={() => {
                                    alert(' !')
                                }}></span>
                                <span onClick={() => {
                                    alert('  !')
                                }}></span>
                            </p>
                        </label>
                    </li>
                    {todoList.map((value, idx) => 
                        <li key={value.id} onClick={() => {
                            //  id    id   
                            // check true 
                            let id = todoList[idx].id;
                            let result = todo.find((todo) => todo.id === id);
                            if(result){
                                result.check = !result.check;
                                localStorage.setItem("todoList", JSON.stringify(todo));
                                setTodoList(todo);
                            }
                        }}>
                            {value.check ? 
                            <p className="active">{value.text}</p>
                            : <p>{value.text}</p>
                        }
                            <p>
                                <span onClick={() => {
                                    alert(' !');
                                }}></span>
                                <span onClick={(e) => todoDelete(idx, value)}></span>
                            </p>
                        </li>
                    )}
                </ul>
             </div>
        </div>
    )
}
export default TodoList;