import { useState } from "react";

const Arr = () => {
    const arr = [
        '  props ',
        '< ={} ( ) />',
        ' ()  props  ',
        '  props  ',
        'props.arr.map((value, index) => <p key={index}>{value}</p>',
        '      ',
        'Array.prototype.map() expects a return value from arrow function',
        '  {}   ()  ',
        'https://velog.io/@ylyl/TIL-Warning-arrow-function-return-value  '
    ];
    const arrObject = [
        {
            title: '(border, bgColor) props    style ',
            content: '   --> {border: black, bgColor: light~  bgColor  {}   ',
        },
        {
            title: '({border, bgColor})',
            content: ' black  lightseagreen   '
        },
        {
            title: '  ',
            content: '  4 ',
        },
        {
            title: '   ',
            content: '  ',
        },
        {
            title: '  930  ',
            content: '  ',
        }
    ];
    const [count, setCount] = useState(0);
    const like = () => {
        setCount(count + 1);
    }
    const hate =() => {
        setCount(count - 1);
    }
    return(
        <div>
            <h2>, </h2>
            <Modal bgColor={'lightgoldenrodyellow'} arr={arr} {...arrObject}/>
            <div style={{textAlign: 'center'}}>
                <p className="ft_red">style  props    </p>
                <p className="ft_red">:    bgColor= 'skyblue'</p>
                <p className="ft_red"> : background: props.bgColor</p>
            </div>
            <Object arrObject={arrObject} like={like} hate={hate} count={count} setCount={setCount}/>
            {/* border={'black'} bgColor={'lightseagreen'} */}
            {/*       ? */}
        </div>
    )
}

const Object = ({arrObject, like, hate, count}) => {
    // arrObject.prototype.map()
    // https://velog.io/@fltxld3/React-%EB%B0%B0%EC%97%B4-%EC%82%AC%EC%9A%A9%ED%95%98%EA%B8%B0-List-%EB%A0%8C%EB%8D%94%EB%A7%81-%EC%A1%B0%ED%9A%8C
    return(
        // style={{border:`1px solid ${border}`, background: bgColor}}
        <div className="md_wrap">
            <button>  (  )</button>
            <button onClick={like}></button><span className="ft_red">({count})</span><button onClick={hate}></button>
            <p style={{fontSize: '30px', marginBottom: '15px'}}></p>
                <ul>
                    {/* {arrObject.map((title, content, index) => (
                    <li key={index}>
                        <p>{title}</p>
                        <div>
                            {content}
                        </div>
                    </li>
                    ))} */}
                </ul>
                {/*     ..? */}
                <ul>
                    <li>
                        <p>{arrObject[0].title}</p>
                        <div>
                            {arrObject[0].content}
                        </div>
                    </li>
                    <br/>
                    <li>
                        <p>{arrObject[1].title}</p>
                        <div>
                            {arrObject[1].content}
                        </div>
                    </li>
                </ul>
        </div>
    )
}

const Modal = (props) => {
    return(
        <div className="md_wrap" style={{background: props.bgColor}}>
            <p style={{fontSize: '30px', marginBottom: '15px'}}>(Modal)</p>
            {/*    props   props  */}
                <ul>
                {props.arr.map((value, index) => 
                (
                    <li key={index}>{value}</li>
                ))}
                </ul>
            <br/>
        </div>
    )
}


export default Arr;